import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

export const Sidebar: React.FC = () => {
  const isMeId: any = useSelector<any>((state) => state.auth.data); //fix any

  return (
    <div className="sidebar">
      <div className="sidebar__menu">
        <ul>
          <li>
            <Link to={`/id${isMeId._id}`} className="sidebar__link">
               
            </Link>
          </li>
          <li>
            <Link to={'/friends'} className="sidebar__link">
               
            </Link>
          </li>
          <li>
            <Link to={'/im'} className="sidebar__link">
               
            </Link>
          </li>
          <li>
            <Link to={'/news'} className="sidebar__link">
               
            </Link>
          </li>
        </ul>
      </div>
      <div className="line-gray"></div>
      <div className="sidebar__menu">
        <ul>
          <li>
            <Link to={'/edit'} className="sidebar__link">
              . 
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};
