import React from 'react';
import TextField from '@mui/material/TextField';
import { useForm } from 'react-hook-form';
import { fetchRegister } from '../redux/slices/auth';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch } from '../redux/store';

export const Auth: React.FC = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    // setError,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      fullName: '',
      email: '',
      password: '',
    },
    mode: 'onChange',
  });

  type SubmitAuth = {
    fullName: string;
    email: string;
    password: string;
  };
  const onSubmit = async (values: SubmitAuth) => {
    const data = await dispatch(fetchRegister(values));

    if (!data.payload) {
      return alert('  ');
    }

    if ('token' in data.payload) {
      window.localStorage.setItem('token', data.payload.token);
      navigate(`/id${data.payload._id}`);
    }
  };

  React.useEffect(() => {
    document.title = '  | ';
  }, []);

  return (
    <div className="auth">
      <div className="auth__headerName"> </div>
      <div className="auth__wrapper">
        <div>
          <span className="bold"></span> -      
            ,      
        </div>
        <div className="auth__text"> </div>
        <div className="line-gray"></div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="auth__container">
            <TextField
              size="small"
              margin="dense"
              error={Boolean(errors.fullName?.message)}
              helperText={errors.fullName?.message}
              {...register('fullName', { required: '  ' })}
              placeholder="   "
            />
            <TextField
              size="small"
              margin="dense"
              error={Boolean(errors.email?.message)}
              helperText={errors.email?.message}
              type="email"
              {...register('email', { required: ' ' })}
              placeholder=" "
            />
            <TextField
              size="small"
              margin="dense"
              error={Boolean(errors.password?.message)}
              helperText={errors.password?.message}
              {...register('password', { required: ' ' })}
              placeholder=" "
            />
            <button disabled={!isValid} type="submit" className="login__button">
              
            </button>
          </div>
        </form>
        <div className="auth__text">   ?</div>
        <div className="line-gray"></div>
        <div className="auth__infoList">
          &#x25A0;  ,    - ,   .
        </div>
        <div className="auth__infoList">
          &#x25A0;    ,   ,    .
        </div>
        <div className="auth__infoList">
          &#x25A0;      ,   .
        </div>
      </div>
    </div>
  );
};
