import { useForm } from 'react-hook-form';
import TextField from '@mui/material/TextField';
import { fetchAuth } from '../redux/slices/auth';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch } from '../redux/store';

export const Login = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    // setError,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      email: '',
      password: '',
    },
    mode: 'onChange',
  });

  type LoginValues = {
    email: string;
    password: string;
  };

  const onSubmit = async (values: LoginValues) => {
    const data: any = await dispatch(fetchAuth(values));

    if (!data.payload) {
      return alert('  ');
    }

    if ('token' in data.payload) {
      window.localStorage.setItem('token', data.payload.token);
      navigate(`/id${data.payload._id}`);
    }
  };

  return (
    <div className="login">
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="login__text"> email:</div>
        <TextField
          size="small"
          margin="dense"
          error={Boolean(errors.email?.message)}
          helperText={errors.email?.message}
          type="email"
          {...register('email', { required: ' ' })}
          fullWidth
        />
        <div className="login__text"> :</div>
        <TextField
          size="small"
          margin="dense"
          error={Boolean(errors.password?.message)}
          helperText={errors.password?.message}
          {...register('password', { required: ' ' })}
          fullWidth
        />
        <button type="submit" disabled={!isValid} className="login__button">
          
        </button>
      </form>
      <div className="line-gray"></div>
      <div className="login__text">   :</div>
      <div>
        <span className="login__grayText">:</span> test1@test.ru
      </div>
      <div>
        <span className="login__grayText">:</span> test1
      </div>
      <div>
        <span className="login__grayText">:</span> test2@test.ru
      </div>
      <div>
        <span className="login__grayText">:</span> test2
      </div>
    </div>
  );
};
